<template>
	<a-layout-sider :trigger="null" collapsible v-model="collapsed">
		<div class="logo" />
		<a-menu theme="dark" mode="inline" :openKeys="openKeys" @openChange="onOpenChange">
            
			<template v-for="item of filter(menulist)">
				<a-menu-item v-if="!item.children" :key="item.path">
					<router-link :to="{path:item.path}">
						<span>
							{{item.meta.title}}
						</span>
					</router-link>
				</a-menu-item>

				<a-sub-menu v-else :key="item.path">
						<span slot="title">
							
							{{item.meta.title}}
							
						</span>

					<a-menu-item v-for="items of item.children" :key="items.path">
						<router-link :to="{path:items.path}">
							{{items.meta.title}}
						</router-link>
					</a-menu-item>
				</a-sub-menu>
			</template>
			
		</a-menu>
	</a-layout-sider>

</template>

<script>
	export default {
		name: "Sider",
		created() {
			let that = this
			that.$eventBus.$on("tarqwe", function() {
				that.collapsed = !that.collapsed
			})
		},
		data() {
			return {
				collapsed: false,
				openKeys:[],
				rootSubmenuKeys:['/userManager','proManager'],
				current:'/'
			};
		},
		computed:{
			menulist(){
				return this.$store.state.menuMdule.menulist
			}
		},
		methods: {
					onOpenChange(openKeys) {//只展开当前父级菜单
						// window.console.log(openKeys)
						const latestOpenKey = openKeys.find(key => this.openKeys.indexOf(key) === -1);
						// window.console.log(latestOpenKey)
						if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
							this.openKeys = openKeys;
						} else {
							this.openKeys = latestOpenKey ? [latestOpenKey] : [];
						}
						// window.console.log(this.openKeys)
					},
					filter(list){//过滤显示
						let arr = []
						arr = list.filter((item)=>{
							return !item.meta.isHidden
						})
						return arr
					}
				}
	}
</script>

