let userModel = {
	namespaced:true,
	state:{
		token:JSON.parse(JSON.stringify(sessionStorage.getItem("token"))) || ""
	},
	mutations:{
		addtoken(state,token){//添加token
			state.token = token
			sessionStorage.setItem("token",JSON.stringify(state.token))
		},
		reflashToken(state,newToken){//刷新token
			state.token = newToken
			sessionStorage.setItem("token",JSON.parse(state.token))
		},
		removeToken(state){
			state.token = ""
			sessionStorage.removeItem("token")
		}
	}
}
export default userModel