import Vue from 'vue'
import VueRouter from 'vue-router'
// import home from '@/views/home/home'
// import login from '@/views/login/login'
// import not_found from "@/views/404/404"
// import list from "@/views/list/list"
// 
const home = () => import('@/views/home/home')//首页
const login = () => import('@/views/login/login')//登录页
const not_found = () => import('@/views/404/404')//404
const result = () => import ('@/views/result/result')//注册页
const usermanger = () => import ('@/views/userManager/index')//用户管理页
const userlist = () => import ('@/views/userManager/userlist')//用户列表页
const useredit = () => import ('@/views/userManager/useredit')//用户编辑页
const parmanger = () => import ('@/views/proManager/index')//商品管理页
const parlist = () => import ('@/views/proManager/prolist')//商品列表页
const paredit = () => import ('@/views/proManager/proedit')//商品编辑页
import store from "@/store/index.js"

Vue.use(VueRouter)

const routes = [
	{
		path:"/",
		name:"home",
		alias:"/home",
		component:home,
		meta:{
			requiresAuth:true,
			title:"首页"
		}
	},
	{
		path:"/login",
		name:"login",
		component:login,
		meta:{
			requiresAuth:false,
			isHidden:true
		}
	},
	{
		path:"*",
		name:"not_found",
		component:not_found,
		meta:{
			requiresAuth:false,
			isHidden:true
		}
	},
	{
		path:"/result",
		name:"result",
		component:result,
		meta:{
			requiresAuth:false,
			isHidden:true
		}
	},
	{
		path:"/usermanger",
		name:"usermanger",
		component:usermanger,
		meta:{
			requiresAuth:true,
			title:"用户管理"
		},
		children:[
			{
				path:"/usermanger/userlist",
				component:userlist,//用户列表页
				meta:{
					requiresAuth:true,
					title:"用户列表"
				},
			},
			{
				path:"/usermanger/useredit",
				component:useredit,//用户编辑页
				meta:{
					requiresAuth:true,
					title:"用户编辑"
				},
			}
		]
	},
	{
		path:"/parmanger",
		name:"parmanger",
		component:parmanger,
		meta:{
			requiresAuth:true,
			title:"商品管理",
		},
		children:[
			{
				path:"/parmanger/prolist",
				component:parlist,//商品列表页
				meta:{
					requiresAuth:true,
					title:"商品列表"
				},
			},
			{
				path:"/parmanger/proedit",
				component:paredit,//商品编辑页
				meta:{
					requiresAuth:true,
					title:"用户编辑"
				},
			}
		]
	}
]

const router = new VueRouter({
	routes
})

router.beforeEach((to,from,next)=>{
	// window.console.log(to)
	if(!to.meta.requiresAuth){
		next()
	}else{
		let token = store.state.userModel.token
		if(!token){
			next({path:"/login"})
		}else{
			store.commit("menuMdule/addmenulist",routes)
			next()
		}
	}
})

export default router