import Vue from 'vue'
import App from './App.vue'
import Antd from 'ant-design-vue';//插件
import 'ant-design-vue/dist/antd.css';//插件样式
import router from "@/router/index.js"//路由
import myplugin from "@/plugin/index.js"//自定义插件
import store from "@/store/index.js"//vuex


Vue.prototype.$eventBus = new Vue()
Vue.config.productionTip = false
Vue.use(Antd)
.use(myplugin)


new Vue({
	router,
	store,
  render: h => h(App),
}).$mount('#app')
